import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ResponseContentType } from '@angular/http';

@Injectable()
export class FrontendService {

    constructor(private http: HttpClient) { }

    login(name: string) {
	let api = "http://api.photosharing.net";
        return this.http.get(api + '/photographer/' + name);
    }

    register(username: string, firstname: string, lastname: string, interests: string) {
	let api = "http://api.photosharing.net";
        return this.http.post(api + '/photographers', {
	    "display_name": username,
	    "first_name": firstname,
	    "interests": [
		interests
	    ],
	    "last_name": lastname
	});
    }

    // Mandatory: add {observe: 'response'} to get Headers in the Observable
    send(username: string, fileToUpload: File) {
	const formData: FormData = new FormData();
	let api = "http://api.photosharing.net";
	formData.append('upfile', fileToUpload, fileToUpload.name);
	return this.http.post(api + '/gallery/' + username, formData,
			      {observe: 'response'})
    }

    getImage(username: string, id: string) {
	let api = "http://api.photosharing.net";
	return this.http.get(api + '/photo/' + username + '/' + id,
			     { responseType: 'blob'});
    }
    
    getImages(id: string) {
	let api = "http://api.photosharing.net";
	return this.http.get(api + '/gallery/' + id + '?offset=0&limit=100');
    }

}



