import { Component, OnInit, Inject } from '@angular/core';
import {MAT_DIALOG_DATA} from '@angular/material';
import {MatDialogRef} from '@angular/material';
import { FrontendService } from '../frontend.service';
import { UserService } from '../user.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-fileupload',
  templateUrl: './fileupload.component.html',
  styleUrls: ['./fileupload.component.css']
})

// see:
// https://stackoverflow.com/questions/47936183/angular-5-file-upload

export class FileuploadComponent implements OnInit {

    fileToUpload: File = null;

    constructor(public thisDialogRef: MatDialogRef<FileuploadComponent>,
		private frontendService: FrontendService,
		private userService: UserService,
		@Inject(MAT_DIALOG_DATA) public data: string) { }

    ngOnInit() {
    }

    onChange (event: any) {
	this.fileToUpload = event.target.files.item(0);

    }

    onCloseConfirm() {
	this.frontendService.send(this.userService.username, this.fileToUpload).
	    subscribe(
	 	res => {
		    console.log(this.fileToUpload);
	 	    console.log(res.headers.get('Location'));
		    this.sendMessage(res.headers.get('Location').split("/").slice(-1)[0]);
	 	},
	 	error => {
	 	    console.log(error);
	 	}
	    );
	this.thisDialogRef.close('Confirm');
    }

    sendMessage(id): void {
        // send message to subscribers via observable subject
        this.userService.sendMessage(id);
    }
 
    clearMessage(): void {
        // clear message
        this.userService.clearMessage();
    }
    onCloseCancel() {
	this.thisDialogRef.close('Cancel');
    }
}



