import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from '../user.service';
import { FrontendService } from '../frontend.service';
import { RegisterComponent } from '../register/register.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
    model: any = {};
    loading:boolean = false;
    returnUrl: string;
    constructor(private userService: UserService,
		private frontendService: FrontendService,
		private route: ActivatedRoute,
		public dialog: MatDialog,
		private router: Router) { }

    ngOnInit() {
    }

    logged() {
	this.loading = false;
	this.userService.logged = true;
    }
	
    login() {
	this.loading = true;
	this.userService.username = this.model.username;
	this.userService.logged = false; // to be removed
	//this.router.navigate(['dummy']);

	this.frontendService.login(this.model.username).
	     subscribe(
	 	data => {
	 	    console.log(data);
	 	    this.logged();
	 	    this.router.navigate(['dummy']);
	 	},
	 	error => {
	 	    console.log(error);
		    this.register();
 		    //this.router.navigate(['dummy']);
	 	}
	     );
    }

    register(): void {
	let dialogRef = this.dialog.open(RegisterComponent, {
	    width: '600px',
	    data: 'This text is passed into the dialog!'
	});

	dialogRef.afterClosed().subscribe(result => {
	    console.log(`Dialog closed: ${result}`);
	    // here we must subscribe to the post REST to register a new photographer
	    // hum ... may be we should subscribe to the REST in the register component !!
	    this.frontendService.register(this.userService.username,
					  this.userService.firstname,
					  this.userService.lastname,
					  this.userService.interests).
		subscribe(
		    data => {
			console.log(data);
 			this.logged();
 			this.router.navigate(['dummy']);
 		    },
	 	    error => {
 			console.log(error);
 			this.router.navigate(['errors']);
 		    }
 		);
	    //this.dialogResult = result;
	});
    }
}



