import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';
import { FrontendService } from '../frontend.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-dummy',
  templateUrl: './dummy.component.html',
  styleUrls: ['./dummy.component.css']
})

export class DummyComponent implements OnInit {
    message: any;
    subscription: Subscription;

    constructor(public userService: UserService,
		private frontendService: FrontendService) {
	this.subscription = this.userService.getMessage().
	    subscribe(message => {
		console.log(message);
		this.frontendService.getImage(this.userService.username, message.text).
		    subscribe(
			res => {
			    console.log(res);
			    this.createImageFromBlob(res);},
			error => {console.log(error);});
		
	    });}

    imageToShow: any;
    imageList: any[] = [];

    createImageFromBlob(image: Blob) {
	let reader = new FileReader();
	reader.addEventListener("load", () => {
            //this.imageToShow = reader.result;
	    this.imageList.push(reader.result);
	}, false);
	
	if (image) {
            reader.readAsDataURL(image);
	}
    }
    
    ngOnDestroy() {
        // unsubscribe to ensure no memory leaks
        this.subscription.unsubscribe();
    }
    
    ngOnInit() {
	this.frontendService.getImages(this.userService.username).
	    subscribe(
		data => {
		    console.log(data);
		    const mydata = data as string[];
		    mydata.forEach ( (element) => {
			// get id from element
			var id = element.split("/").pop();
			this.frontendService.getImage(this.userService.username, id).subscribe(
			    res => {
				console.log(res);
				this.createImageFromBlob(res);
			    },
			    error => {
				console.log(error);
			    }
			);
		    });
		},
		error => {
		    console.log(error);
		}
	    );
    }

}



